body{
    font-family: "Roboto", sans-serif;
    color:#212529;
}

#video-bg{
    position: absolute;
        right: 0;
        bottom: 0;
        min-width: 100%;
        min-height: 100%;
        z-index: -10;
}
.navbar.is-fixed-top {
    position: fixed;
    right: -3rem;
    top: -5rem;
    height: 11rem;
    width: 11rem;
    left: auto;
    border-radius: 50%;
    background-color: #ee003a;
}

.logo-icon{
    font-size: 8rem;
    position: absolute;
    color: #fff;
    transform: rotate(30deg);
    left: 1rem;
    top: 0.5rem;

}
.overlay-parent{
    position: relative;
}
.overlay{
    position: absolute;
    background-color: #3b3f4024;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
    border-radius: 20px;
}

#welcome{
    background-image: url("./images/header-img.png");
    background-repeat: no-repeat;
    background-position: 55% 20%;
}

.welcome-text {
    margin-bottom: 7rem;
    font-size: 3rem;
}

.list-title{
    margin-top: 5rem;
}

.table th,
td {
    text-align: center !important;
}

tr:first-child {
    text-align: left !important;
}


.card h2{
    text-align: center;
    font-weight: 700;
    font-size: 1.5rem;
    margin-top: 1rem;
}

.card-top{
    text-align: left;
    padding: 1rem 0;
    width: 80%;
    margin: 0 auto;
    font-size: 1.2rem;
    border-top:solid 1px #3b3f40;
}

.card-button {
    width: 80%;
    margin: 0.5rem auto;
}

.zoom-card {
    transition: all 250ms ease-in;

    &:hover {
        transform: scale(1.05);
    }
}

.shadow-card {
    transition: all 250ms ease-in;

    &:hover {
        box-shadow: 0 0 25px black;
        z-index: 1;
    }
}

/* ==========================================================================
   Cards Course
   ========================================================================== */
.card {
    padding-bottom: 1rem;
    border: 0;
    border-radius: 10px;
}

.navbar{
    position: relative;
    z-index: 10;
}
#navbarNav{
    justify-content: end;
    padding:1rem
}
#navbarNav ul{
    justify-content: space-around;
    padding: 1rem;
    min-width: 80%;
}
.nav-link{
    font-size: 1.2rem;
    color: #000;
    text-transform: uppercase;
    font-weight: 900;
    padding: 0 1rem;
    text-align: center;
}

#nav-img{
    position: absolute;
    top:0;
    left:0;
    width:180px;
    height: 160px;
}
#nav-img img{
    width:220px
}
.logo-link{
    position: absolute;
    // opacity:0;
    top: -4rem;
    left: -1.5rem;
    width: 12rem;
    height:12rem;
    border-radius: 50%;
}
.clickable{
    cursor: pointer;
}
.underline {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
}

.underline:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: #000;
    height: 4px;
    -webkit-transition-property: left, right;
    transition-property: left, right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.underline:hover:before,
.underline:focus:before,
.underline:active:before {
    left: 0;
    right: 0;
}


@media screen and (max-width:768px) {
    #nav-img img{
        width:180px
    }

    .logo-icon{
    font-size: 7rem;
    position: absolute;
    color: #fff;
    transform: rotate(30deg);
    left: 1rem;
    top: 0;

}
}















